
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
data () {
return {
redirect: '', // 从哪个页面来
baseUrl: process.env.DOMAIN_IP,
amount: '',
orderNo: '' 
}
},
methods: {
goBack () {
if (this.redirect) { // 从下单流程过来
this.$router.push({
path: '/' + this.redirect
})
} else {
this.$util.getMachine(4)
}
},
toOnline () {
if (this.$util.appOpenUrl(this.baseUrl + '/mobile-hsy/index.html#/onlineRecharge?amount=' + this.amount + '&orderNo=' + this.orderNo, '线上充值')) {
this.$router.push({
path: '/onlineRecharge',
query: {
amount: this.amount,
orderNo: this.orderNo
}
})
}
},
toOffline () {
this.$router.push({
path: '/offlineRecharge',
query: {
amount: this.amount,
orderNo: this.orderNo
}
})
},
toICBC () {
this.$router.push({
path: '/ICBCrecharge'
})
}
},
created () {
this.redirect = this.$route.query.redirect
this.orderNo = this.$route.query.orderNo
if (this.$route.query.amount) {
this.amount = this.$route.query.amount
}
},
components: {
XHeader,
Group,
Cell
}
}



